import React, { useState } from "react";
import { TextField, Button } from "@mui/material";
import Box from "@mui/material/Box";

const Item = ({item, onChange, currentStatus}) => {
      const handleInputchange = (event) => {
            const {name, value} = event.target;
            onChange({...item, [name]: value})
      };

      const [selectedFile, setSelectedFile] = useState(null)
      const handleFileChange = (event) => {
            setSelectedFile(event.target.files[0]);
      };

      console.log(item);
      return (
            <div>
                  <Box display="flex" justifyContent="space-between" alignItems="center" p={1} width="100%">
                        <TextField
                              label="Item name"
                              name="title"
                              value={item.title}
                              onChange={handleInputchange}
                              variant="outlined"
                              fullWidth
                              margin="normal"
                              disabled
                              size="small"
                        />
                        {/* <TextField
                              label="Price Per Unit"
                              name="pricePerUnit"
                              value={item.pricePerUnit}
                              onChange={handleInputchange}
                              variant="outlined"
                              fullWidth
                              margin="normal"
                              disabled
                              size="small"
                        />
                        <TextField
                              label="Weight per unit"
                              name="weightPerUnit"
                              value={item.WeightPerUnit}
                              onChange={handleInputchange}
                              variant="outlined"
                              fullWidth
                              margin="normal"
                              disabled
                              size="small"
                        /> */}
                        
                        <TextField
                              label="Approved Quantity"
                              name="approvedQuantity"
                              value={item.approvedQuantity}
                              onChange={handleInputchange}
                              variant="outlined"
                              fullWidth
                              margin="normal"
                              disabled
                              size="small"
                        />

                        <TextField
                              label="Gross Weight"
                              name="grossWeight"
                              value={item.approvedQuantity * item.grossWeightPerUnit}
                              onChange={handleInputchange}
                              variant="outlined"
                              fullWidth
                              margin="normal"
                              disabled
                              size="small"
                        />

                        {currentStatus >= 1 && <TextField
                              label="Weight At Loading"
                              name="WeightAtLoading"
                              value={item.weightAtLoading}
                              onChange={handleInputchange}
                              variant="outlined"
                              margin="normal"
                              fullWidth
                              size="small"
                              disabled={currentStatus === 2 ? true : false}/>}

                        {currentStatus === 2 && <TextField
                              label="Weight At Unloading"
                              name="WeightAtUnloading"
                              value={item.weightAtUnloading}
                              onChange={handleInputchange}
                              variant="outlined"
                              fullWidth
                              size="small"
                              margin="normal"
                              />}
                  </Box>

                  <Box 
                        display="flex"
                        flexDirection="row"
                        justifyContent="space-evenly"
                        alignItems="center"
                        maxWidth="50%"
                        marginBottom="2em"
                  >

                        <input
                              type="file"
                              accept=".jpeg, .jpg, .png"
                              onChange={handleFileChange}
                              style={{ display: 'none' }}
                              id="fileInput"
                        />
                        <label htmlFor="fileInput">
                              <Button variant="contained" component="span" size="small">
                                    Upload Image
                              </Button>
                        </label>

                        <TextField
                              label="Selected File"
                              value={selectedFile ? selectedFile.name : ''}
                              variant="outlined"
                              size="small"
                              disabled
                        />
                  </Box>
            </div>
      )
}

export default Item;
