import React, {useState} from "react";
import Timeline from "@mui/lab/Timeline";
import { 
      TimelineItem, 
      TimelineSeparator,  
      TimelineConnector,
      TimelineContent,
      TimelineDot,
} from "@mui/lab";
import { Typography } from "@mui/material";
import Box from "@mui/material/Box";
import { Button } from "@mui/material";
import { Modal } from "@mui/material";
import AuditorForm from "./auditorform";

const OrderStatusTimeline = (props) => {
      console.log("Inside order status timeline")
      console.log(props.orderDetails)
      const orderStatus = ["Proposal Created", "Approved by Auditor", "Loaded By Contractor", "Reached Contractor Warehouse"]
      const [auditorFormOpen, setAuditorFormOpen] = useState(false);
      const handleAuditorFormOpen = () => setAuditorFormOpen(true);
      const handleAuditorFormClose = () => setAuditorFormOpen(false);
      
      const renderTimelineItem = (item, index) => {
            return (
                  <TimelineItem>
                              <TimelineSeparator>
                                    <TimelineDot color={index <= props.currentStatus ? "success" : "grey"} />
                                    {index !== 3 && <TimelineConnector />}
                              </TimelineSeparator>
      
                              <TimelineContent>
                                    <Typography variant="body1" component="p">
                                          {item}
                                    </Typography>
                              </TimelineContent>
                  </TimelineItem>
            )
      }

      return (
            <Box display="flex" justifyContent="space-between" alignItems="center" p={1} width="100%">
                  <Timeline> 
                        {orderStatus.map(renderTimelineItem)}
                  </Timeline>
                  <Box
                        display="flex"
                        flexDirection="column"
                        justifyContent="space-evenly"
                        alignItems="center"
                        sx={2}
                        p={1}
                        height="200px"
                        maxWidth="100%" 
                  >
                        {props.currentStatus === 0 && <Button variant="contained" onClick={handleAuditorFormOpen}>
                              Approve Proposal
                        </Button>}
                        <Button variant="contained">
                              View Proposal Details
                        </Button>
                  </Box>

                  <Modal
                        open={auditorFormOpen}
                        onClose={handleAuditorFormClose}
                        aria-labelledby="Auditor Approval Form"
                  >
                        <Box sx={{
                              position: 'abosute',
                              width: "80%",
                              bgcolor: 'background.paper',
                              boxShadow: 24,
                              p: 4
                        }}>
                              <Typography variant="h6" id="auditor-modal-title">
                                    Auditor Approval Form
                              </Typography>
                              <AuditorForm currentOrderDetails={props.orderDetails} />
                              <Button onClick={handleAuditorFormClose} sx={{mt: 2}}>
                                    Close
                              </Button>
                        </Box>
                        
                  </Modal>
            </Box>
      )
}

export default OrderStatusTimeline;
