import React, {useState, useContext} from "react";
import { 
      Button,
      TextField,
      Box,
      Typography
 } from "@mui/material";
 import Item from "./item";

 const AuditorForm = ( {currentOrderDetails }) => {

      const [items, setItems] = useState(currentOrderDetails.items)

      const updateItem = (updatedItem) => {
            setItems(
                  items.map( (item) =>
                        item.id === updatedItem.id ? updatedItem : item
                  )
            )
      }

      const handleSubmit = (event) => {
            event.preentDefault();
      
      };

      return (
            <Box
                  display="flex"
                  flexDirection="column"
                  justifyContent="space-evenly"
                  alignItems="center"
                  maxWidth="100%"
                  p={1}
                  s={1}
            >
                  <form onSubmit={handleSubmit}>
                        <TextField
                              label="Warehouse"
                              name="Warehouse"
                              value={currentOrderDetails.Warehouse}
                              variant="outlined"
                              margin="normal"
                              fullWidth
                              disabled
                        />
                        <TextField
                              label="Total Quantity"
                              name="totalQuantity"
                              value={currentOrderDetails.totalQuantity}
                              variant="outlined"
                              margin="normal"
                              disabled
                              fullWidth
                        />
                        
                        <Box>
                              {items.map((item) => (
                                    <Item key={item.id} item={item} onChange={updateItem} />
                              ))}
                        </Box>

                        <Button type="submit" variant="contained" color="primary" style={{ marginTop: "4em"}} >
                                    Submit
                        </Button>

                  </form>
            </Box>
      )
 }

 export default AuditorForm;
