import React, {useState} from "react";
import ButtonAppBar from "./AppBar";
import Button from "@mui/material/Button"
import { Box, Typography } from "@mui/material";
import Modal from "@mui/material/Modal"
import CreateOrderForm from "./forms";
import OrderList from "./orders";
import { Link } from "react-router-dom";

const DistributerDashboard = () => {

      const [createOrderFormOpen, setCreateOrderFormOpen] = useState(false);
      const handleCreateOrderFormOpen = () => setCreateOrderFormOpen(true);
      const handleCreateOrderFormClose = () => setCreateOrderFormOpen(false);

      return (
            <div>
                  <ButtonAppBar />
                  <Box display="flex" justifyContent="flex-end" alignItems="flex-end"
                  sx={{ margin: '0 auto', padding: 2 }}
                  >
                        <Button
                              variant="contained"
                              onClick={handleCreateOrderFormOpen}
                        >
                              Create Proposal
                        </Button>
                  </Box>
                  <Modal
                              open={createOrderFormOpen}
                              onClose={handleCreateOrderFormClose}
                              aria-labelledby="Create Proposal Form"
                        >
                        <Box sx={{
                              position: 'absolute',
                              top: '50%',
                              left: '50%',
                              transform: 'translate(-50%, -50%)',
                              width: "80%",
                              bgcolor: 'background.paper',
                              boxShadow: 24,
                              p: 4,
                              }}>
                              <Typography variant="h6" id="modal-title">
                                    Create Proposal
                              </Typography>
                              <CreateOrderForm onClose={handleCreateOrderFormClose} />
                              <Button onClick={handleCreateOrderFormClose} sx={{ mt: 2 }}>
                                    Close
                              </Button>
                        </Box>
                  </Modal>
                  
                  <Box p={4}>
                        <OrderList />
                  </Box>

                  <Box
                        display="flex"
                        alignItems="center"
                        maxWidth
                        p={2}>

                        <Link to="/auditordashboard">
                              <Button variant="contained" color="primary">
                                    To Auditor Dashboard
                              </Button>
                        </Link>

                  </Box>
                  
            </div>
      );
}

export default DistributerDashboard;
