import React, {useState, useContext} from "react";
import {
      Button,
      Box,
      TextField,
} from "@mui/material";
import Item from "./item";

const ContractorLoadingForm = ( { currentOrderDetails }) => {

      const [orderDetails, setOrderDetails] = useState({
            ...currentOrderDetails,
      });

      const [selectedFile, setSelectedFile] = useState(null);

      const handleFileChange = (event) => {
            setSelectedFile(event.target.files[0]);
      };

      const updateOrderDetails = (event) => {
            const {name, value} = event.target;
            setOrderDetails( (prevData) => ({
                  ...prevData,
                  [name]: value,
            }));
      };

      const handleSubmit = (event) => {
            event.preventDefault();
            
            console.log(orderDetails);
            console.log('Selected File:', selectedFile)
      };

      return (
            <Box
                  dispaly="flex"
                  flexDirection="column"
                  justifyContent="space-evenly"
                  alignItems="center"
                  maxWidth="100%"
                  p={1}
                  s={1}
            >
                  <form onSubmit={handleSubmit}>
                        <TextField
                              label="Warehouse"
                              name="Warehouse"
                              value={orderDetails.Warehouse}
                              variant="outlined"
                              fullWidth
                              margin="normal"
                              disabled />

                        <TextField
                              label="Total Quantity"
                              name="totalQuantity"
                              value={orderDetails.totalQuantity}
                              variant="outlined"
                              fullWidth
                              margin="normal"
                              disabled />

                        <Box>
                              {orderDetails.items.map((item) => <Item key={item.id} item={item} currentStatus={orderDetails.currentStatus} />)}
                        </Box>
                        
                        <Button type="submit" variant="contained" color="primary">
                              Submit
                        </Button>
                  </form>
            </Box>
      )
};

export default ContractorLoadingForm;
