import React, {useContext, useState} from "react";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardContent from "@mui/material/CardContent";
import CardActions from "@mui/material/CardActions";
import Collapse from "@mui/material/Collapse";
import { Box, IconButton } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore"
import OrderStatusTimeline from "./timeline";
import OrderContext from "../order-context";

const OrderCard = ({ order, isExpanded, onExpand, index }) => {
      return (
                        <Card>
                              <Box display="flex" justifycontent="space-between" alignItems="center" p={1} width="100%">
                                    <CardHeader title={"Proposal #" + order.removalOrderID} />
                                    <CardActions disableSpacing>
                                          <IconButton
                                                onClick={() => { onExpand(index)} }
                                                aria-expanded={isExpanded === index}
                                                aria-label="Show more"
                                          >
                                                <ExpandMoreIcon />
                                          </IconButton>
                                    </CardActions>
                              </Box>
                              
            
                              <Collapse in={isExpanded === index} timeout="auto" unmountOnExit>
                                    <CardContent>
                                          <OrderStatusTimeline currentStatus={order.currentStatus}/>
                                    </CardContent>
                              </Collapse>
                        </Card>
      )
};

const OrderList = () => {
      const { orders } = useContext(OrderContext)

      const [expandedOrder, setExpandedOrder] = useState(null);

      const toggleExpandOrder = (index) => {
            if(expandedOrder === index) {
                  setExpandedOrder(null);
            } else {
                  setExpandedOrder(index);
            }
      }

      const currentOrderList = orders.slice();

      return (
            <div>
                  {currentOrderList.reverse().map((order, index) => {
                        return (
                              <OrderCard order={order} isExpanded={expandedOrder} onExpand={toggleExpandOrder} index={index} key={index} />
                        )
                  })}
            </div>
      )
};
