import React, { useState } from "react";
import { TextField, MenuItem } from "@mui/material";
import Box from "@mui/material/Box";

const Item = ({item, onChange}) => {
      const itemNameDropdownList = [
            "Agarbatti",
            "Garment",
            "Chocolate",

      ]
      const pricePerUnitList = [100, 300, 50];
      const weightPerUnitList = [10, 250, 20];
      const grossWeightPerUnitList = [11, 252, 21];

      const [selectedItemName, setSelectedItemName] = useState(null);
      const [selectedItemPricePerUnit, setSelectedItemPricePerUnit] = useState(0);
      const [selectedItemWeightPerUnit, setSelectedItemWeightPerUnit] = useState(0);
      const [selectedItemGrossWeightPerUnit, setSelectedItemGrossWeightPerUnit] = useState(0);

      const handleSelectedItemNameChange = (event) => {
            const selectedName = event.target.value;

            setSelectedItemName(selectedName);

            const selectedItemIndex = itemNameDropdownList.indexOf(selectedName);
            
            setSelectedItemPricePerUnit(pricePerUnitList[selectedItemIndex]);
            setSelectedItemWeightPerUnit(weightPerUnitList[selectedItemIndex]);
            setSelectedItemGrossWeightPerUnit(grossWeightPerUnitList[selectedItemIndex]);

            const dummyEvent = {
                  target: {
                        name: "title",
                        value: selectedName
                  }
            };
            handleInputchange(dummyEvent);

            dummyEvent.target.name = "pricePerUnit";
            dummyEvent.target.value = pricePerUnitList[selectedItemIndex];
            handleInputchange(dummyEvent);

            dummyEvent.target.name = "WeightPerUnit";
            dummyEvent.target.value = weightPerUnitList[selectedItemIndex];
            handleInputchange(dummyEvent)

            dummyEvent.target.name = "grossWeightPerUnit";
            dummyEvent.target.value = grossWeightPerUnitList[selectedItemIndex];
            handleInputchange(dummyEvent)
      }

      const handleInputchange = (event) => {
            const {name, value} = event.target;
            onChange({...item, [name]: value})
      };

      return (
            <Box display="flex" justifyContent="space-between" alignItems="center" p={1} width="100%">
                  {/* <TextField
                        label="Item name"
                        name="title"
                        value={item.title}
                        onChange={handleInputchange}
                        variant="outlined"
                        fullWidth
                        margin="normal"
                  /> */}
                  <TextField
                        select
                        label="Select Item Name"
                        name="title"
                        value={selectedItemName}
                        onChange={handleSelectedItemNameChange}
                        variant="outlined"
                        fullWidth
                        margin="normal"
                  >
                        {itemNameDropdownList.map( (item, index) => <MenuItem value={item}>{item}</MenuItem>)}
                  </TextField>
                  <TextField
                        label="Price Per Unit"
                        name="pricePerUnit"
                        value={selectedItemPricePerUnit}
                        onChange={handleInputchange}
                        variant="outlined"
                        fullWidth
                        margin="normal"
                        disabled
                  />
                  <TextField
                        label="Weight per unit"
                        name="weightPerUnit"
                        value={selectedItemWeightPerUnit}
                        onChange={handleInputchange}
                        variant="outlined"
                        fullWidth
                        margin="normal"
                        disabled
                  />
                  <TextField
                        label="Gross Weight Per Unit"
                        name="grossWeightPerUnit"
                        value={selectedItemGrossWeightPerUnit}
                        onChange={handleInputchange}
                        variant="outlined"
                        fullWidth
                        margin="normal"
                  />
                  <TextField
                        label="Quantity"
                        name="quantity"
                        value={item.quantity}
                        onChange={handleInputchange}
                        variant="outlined"
                        fullWidth
                        margin="normal"
                  />
            </Box>
      )
}

export default Item;
