import React, {useState} from "react";
import Timeline from "@mui/lab/Timeline";
import { 
      TimelineItem, 
      TimelineSeparator,  
      TimelineConnector,
      TimelineContent,
      TimelineDot,
} from "@mui/lab";
import { Typography } from "@mui/material";
import Box from "@mui/material/Box";
import { Button } from "@mui/material";
import { Modal } from "@mui/material";
import ContractorLoadingForm from "./contractor-loading-form";

const OrderStatusTimeline = (props) => {
      console.log("Inside order status timeline")
      console.log(props.orderDetails)
      const orderStatus = ["Proposal Created", "Approved by Auditor", "Loaded By Contractor", "Reached Contractor Warehouse"]
      const [contractorFormOpen, setContractorFormOpen] = useState(false);
      const handleContractorFormOpen = () => setContractorFormOpen(true);
      const handleContractorFormClose = () => setContractorFormOpen(false);
      
      const renderTimelineItem = (item, index) => {
            return (
                  <TimelineItem>
                              <TimelineSeparator>
                                    <TimelineDot color={index <= props.currentStatus ? "success" : "grey"} />
                                    {index !== 3 && <TimelineConnector />}
                              </TimelineSeparator>
      
                              <TimelineContent>
                                    <Typography variant="body1" component="p">
                                          {item}
                                    </Typography>
                              </TimelineContent>
                  </TimelineItem>
            )
      }

      return (
            <Box display="flex" justifyContent="space-between" alignItems="center" p={1} width="100%">
                  <Timeline> 
                        {orderStatus.map(renderTimelineItem)}
                  </Timeline>
                  <Box 
                        display="flex" 
                        flexDirection="column"
                        justifyContent="space-evenly"
                        alignItems="center" 
                        sx={2} 
                        p={1}
                        height="200px"
                        maxWidth="100%">
                        {props.currentStatus >= 1 && props.currentStatus < 3 && <Button variant="contained" onClick={handleContractorFormOpen}>
                              {props.currentStatus === 2 ? "Unload Proposal" : "Load Proposal"}
                        </Button>}
                        <Button variant="contained">
                              View Proposal Details
                        </Button>
                  </Box>

                  <Modal
                        open={contractorFormOpen}
                        onClose={handleContractorFormClose}
                        aria-labelledby="Contractor Loading Form"
                  >
                        <Box 
                              sx={{
                                    position: 'abosute',
                                    width: "80%",
                                    bgcolor: 'background.paper',
                                    boxShadow: 24,
                                    p: 4
                              }}
                        >
                              <Typography variant="h6" id="contractor-loading-modal-title">
                                    {props.currentStatus === 2 ? "Contractor Unloading Form" : "Contractor Loading Form"}
                              </Typography>
                              <ContractorLoadingForm currentOrderDetails={props.orderDetails} />
                              <Button onClick={handleContractorFormClose} sx={{mt: 2}}>
                                    Close
                              </Button>
                        </Box>
                        
                  </Modal>
            </Box>
      )
}

export default OrderStatusTimeline;
